<template>
  <div class="page-card-content" v-loading="loading">
    <div class="top-area">
      <el-form ref="searchForm" :model="searchForm" inline size="middle" style="width:100%">
        <div style="display:flex">
          <div style="flex:1">
            <el-form-item label="平台名称">
              <el-input
                v-model="searchForm.keywordName"
                placeholder="输入搜索关键字"
                clearable
                class="mr50"
              >
              </el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item>
              <el-button type="primary" @click="handleRowClick('add')">新增</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTableData(true)">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="warning" @click="resetData()">重置</el-button>
            </el-form-item>
          </div>
        </div>
      </el-form>
    </div>

    <el-table
      :data="tableData"
      stripe
      row-key="id"
      :header-cell-style="{ background: '#f7f8fb' }"
      height="620"
      @select="handlSelect"
      @select-all="handlSelect"
    >
      <template slot="empty">
        <div class="empty-img">
          <img src="@/assets/img/empty.png" />
          <p>暂无数据</p>
        </div>
      </template>
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column label="平台名称" show-overflow-tooltip prop="name" width="150">
      </el-table-column>
      <el-table-column label="平台描述" prop="desc" width="150"> </el-table-column>
      <el-table-column label="链接" prop="url"> </el-table-column>
      <el-table-column label="APPKey" prop="appKey" width="160"> </el-table-column>
      <el-table-column label="AppSecret" prop="appSecret"> </el-table-column>
      <el-table-column label="创建人" prop="creatorName" width="150"> </el-table-column>
      <el-table-column label="创建时间" prop="createTime" width="180"> </el-table-column>
      <el-table-column label="操作" width="180">
        <template slot-scope="scope">
          <el-button type="text" @click="handleRowClick('editor', scope.row)">编辑</el-button>
          <el-button type="text" @click="handleRowClick('config', scope.row)">菜单设置</el-button>
          <el-button type="text" @click="handleRowClick('delete', scope.row)" style="color:#f56c6c"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      class="main-pagination"
      :total="pageInfo.total"
      :current-page="searchForm.pageNum"
      :page-size="searchForm.pageSize"
      :page-sizes="pageInfo.pageSizes"
      @current-change="handlePageChanged"
      @size-change="handlePageSizeChanged"
      background
      layout="total, sizes, prev, pager, next"
    />
    <!-- 新增-->
    <el-dialog
      :title="operationType == 'editor' ? '编辑' : '新增'"
      width="600px"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      label-width="120px"
      style="margin-top:-20px"
    >
      <el-form ref="addForm" :model="addForm" label-width="100px" :rules="formRules">
        <el-form-item label="平台名称" prop="name">
          <el-input
            v-model="addForm.name"
            placeholder="请输入内容"
            class="labelWidth"
            maxlength="10"
          ></el-input>
        </el-form-item>
        <el-form-item label="链接" prop="url">
          <el-input v-model="addForm.url" placeholder="请输入内容" class="labelWidth"></el-input>
        </el-form-item>
        <el-form-item label="AppKey" prop="appKey" v-if="operationType == 'editor'">
          <el-input
            v-model="addForm.appKey"
            placeholder="请输入内容"
            class="labelWidth"
            :disabled="operationType == 'editor'"
          ></el-input>
        </el-form-item>
        <el-form-item label="AppSecret" prop="appSecret" v-if="operationType == 'editor'">
          <el-input
            v-model="addForm.appSecret"
            placeholder="请输入内容"
            class="labelWidth"
            :disabled="operationType == 'editor'"
          ></el-input>
          <icon
            class="el-icon-refresh"
            style="margin-left:10px;cursor: pointer"
            @click="getPlatformDetail"
          ></icon>
        </el-form-item>
        <el-form-item label="平台描述" prop="desc">
          <el-input
            v-model="addForm.desc"
            placeholder="请输入内容"
            class="labelWidth"
            maxlength="150"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm" style="float: right;">
            保存
          </el-button>
          <el-button
            type="primary"
            @click="submitForm(1)"
            plain
            style="float: right;margin-right:15px"
          >
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from '@/api/index.js'

export default {
  filters: {},
  data() {
    // const urlValidate = (mirror, value, callback) => {
    //   console.log(value)
    //   if (value != '') {
    //     debugger
    //     var url = value
    //     var reg = /^http:/
    //     if (!reg.test(url)) {
    //       callback(new Error('请输入正确的链接地址'))
    //       return
    //     }
    //   } else {
    //     callback()
    //   }
    // }
    return {
      loading: false,
      tableData: [],
      pageInfo: {
        pageSizes: [10, 20, 50],
        total: 0
      },
      searchForm: {
        keywordName: '',
        pageNum: 1,
        pageSize: 10
      },
      dialogVisible: false,
      operationType: 'add',
      addForm: {},
      formRules: {
        name: [
          {
            trigger: 'blur',
            message: '请输入正确名称（支持中文，英文大小写）',
            required: true,
            pattern: '^[a-zA-Z\\u4e00-\\u9fa5]+$'
          }
        ],
        url: [
          {
            trigger: 'blur',
            message: '请输入正确链接地址',
            required: true,
            pattern: /(http|https):\/\/([\w.]+\/?)\S*/
            // validator: urlValidate
          }
        ]
      },
      platformId: 0
    }
  },
  created() {
    this.getTableData()
  },
  mounted() {},
  methods: {
    // 平台详情
    getPlatformDetail() {
      API.getPlatformDetail({ id: this.platformId }).then(res => {
        if (res.status == 200) {
          this.addForm = res.data
        } else {
          this.$message.error(res.message)
        }
      })
    },
    submitForm(type) {
      if (type == 1) {
        this.$refs.addForm.resetFields()
        this.addForm.desc = ''
        this.dialogVisible = false
      } else {
        this.$refs.addForm.validate(valid => {
          if (valid) {
            if (this.addForm.id) {
              API.getPlatformModify(this.addForm).then(res => {
                if (res.status == 200) {
                  this.$refs.addForm.resetFields()
                  this.dialogVisible = false
                  this.$message.success('保存成功')
                  this.getTableData()
                } else {
                  this.$message.error(res.message)
                }
              })
            } else {
              API.getPlatformAdd(this.addForm).then(res => {
                if (res.status == 200) {
                  this.$refs.addForm.resetFields()
                  this.dialogVisible = false
                  this.$message.success('保存成功')
                  this.getTableData()
                } else {
                  this.$message.error(res.message)
                }
              })
            }
          } else {
            this.$message.error('请检查表单内容是否填写完整')
          }
        })
      }
    },
    handlSelect(row) {
      this.clickRow = row
    },
    // 重置
    resetData() {
      this.searchForm = {
        keywordName: '',
        pageNum: 1,
        pageSize: 10
      }
      this.getTableData()
    },
    handleRowClick(type, row) {
      switch (type) {
        case 'add':
          this.dialogVisible = true
          this.operationType = 'add'
          if (this.addForm.id) {
            delete this.addForm.id
          }
          break
        case 'editor':
          this.platformId = row.id
          this.operationType = type
          this.dialogVisible = true
          this.getPlatformDetail()
          break
        case 'delete':
          this.getPlatformDelete(row.id)
          break
        case 'config': //菜单设置
          this.$router.push({
            name: 'menuManage',
            query: { id: row.id, url: row.url, desc: row.desc, name: row.name }
          })
          break
      }
    },
    //删除
    getPlatformDelete(id) {
      const ids = []
      ids.push(id)
      this.$confirm('删除以后数据不可恢复，请谨慎操作！', '删除', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.loading = true
        API.getPlatformDelete({ ids: ids })
          .then(res => {
            if (res.status == 200) {
              this.$message.success(res.message)
              this.getTableData()
            } else {
              this.$message.error(res.message)
            }
          })
          .finally(() => {
            this.loading = false
          })
      })
    },
    getTableData(init) {
      this.loading = true
      if (init) {
        this.searchForm.pageNum = 1
        this.searchForm.pageSize = 10
      }
      const form = JSON.parse(JSON.stringify(this.searchForm))
      if (!form.keywordName) {
        delete form.keywordName
      }
      API.getPlatformListPage(form)
        .then(res => {
          if (res.status == 200) {
            this.pageInfo.total = res.data.total
            this.tableData = res.data.records
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    handlePageChanged(value) {
      this.searchForm.pageNum = value
      this.getTableData(false)
    },
    handlePageSizeChanged(value) {
      this.searchForm.pageSize = value
      this.getTableData(false)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-table-column--selection .cell {
  padding-left: 10px;
}
#code {
  height: 500px;
  width: 500px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
  border: none;
}
::v-deep .el-upload-list {
  max-height: 400px;
  overflow: auto;
}
.mr50 {
  margin-right: 60px;
  width: 250px;
}
::v-deep .el-upload-dragger {
  width: 96px;
  height: 96px;
}
::v-deep .el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
::v-deep .el-upload--picture-card {
  width: 96px;
  height: 96px;
  line-height: 96px;
}

.labelWidth {
  width: 400px !important;
}
</style>
